import React, { useContext } from 'react';
import './mywork.css';
import { Card } from '../../card/Card';
import Projet1 from '../../../assets/website-dribble.png';
import Projet2 from '../../../assets/Capture.PNG';
import { LanguageContext } from '../../../context/language/LanguageContext';
import { words } from '../../../langues/';

export const MyWork = () => {
  const languageContext = useContext(LanguageContext);
  const { language } = languageContext;
  const { t2 } = words[language];
  return (
    <section id='my-work'>
      <div className='title-section'>
        <h1>
          <ul>
            <li value={2}> {t2}</li>
          </ul>
        </h1>
        {/* <div className='separator'></div> */}
      </div>
      <article>
        <Card Title='Find & Do' Image={Projet2} />
        <Card Title='Integration' Image={Projet1} />
      </article>
    </section>
  );
};
